import Vue from 'vue'
import Router from 'vue-router'
import Resource from 'vue-resource'
import index from '@/components/index'
import sell from '@/components/sell'

Vue.use(Router);//装载插件
Vue.use(Resource);//装载插件

export default new Router({
  routes: [
    {
      path: '/',
      name:"首页",
      component: index
    },
    {
      path: '/sell',
      name:"卖车",
      component: sell
    }
  ]
})



