<template>
	<div>
		<ul class="filter">
		    <li @click="searchSlider(0)" class="Jssel" id="filter">筛选</li>
		    <li @click="searchSlider(1)" class="Jssel" id="brand">品牌</li>
		    <li @click="searchSlider(2)" class="Jssel" id="sort">类型</li>
		</ul>

		<div class="fix" :class="{fixIn:showHideBool,fixOut:!showHideBool}" @click="showHideBool=false">
		    <!--筛选-->
		    <ul id="filterUl" :style="{display: sliderDisplay[0]}" @click.stop>
		        <li v-for="str in filterNameArr" @click="filterSearch(str)">{{str}}</li>
		    </ul>


		    <!---品牌 车系 车型-->
		    <ul id="brandUl" :style="{display: sliderDisplay[1]}" @click.stop>
		        <li>
		            <a href="javascript:void(0)">奔驰</a>
		            <ul style="display: none;">
		                <li>
		                    <a href="javascript:void(0)">C系</a>
		                    <ul style="display: none;">
		                        <li><a href="javascript:void(0)">c63</a></li>
		                        <li><a href="javascript:void(0)">c180</a></li>
		                        <li><a href="javascript:void(0)">c200</a></li>
		                    </ul>
		                </li>
		                <li>
		                    <a href="javascript:void(0)">E系</a>
		                    <ul style="display: none;">
		                        <li><a href="javascript:void(0)">E63</a></li>
		                        <li><a href="javascript:void(0)">E180</a></li>
		                        <li><a href="javascript:void(0)">E200</a></li>
		                    </ul>
		                </li>
		            </ul>
		        </li>
		        <li>
		            <a href="javascript:void(0)">宝马</a>
		            <ul style="display: none;">
		                <li>
		                    <a href="javascript:void(0)">C系</a>
		                    <ul style="display: none;">
		                        <li><a href="javascript:void(0)">c63</a></li>
		                        <li><a href="javascript:void(0)">c180</a></li>
		                        <li><a href="javascript:void(0)">c200</a></li>
		                    </ul>
		                </li>
		                <li>
		                    <a href="javascript:void(0)">E系</a>
		                    <ul style="display: none;">
		                        <li><a href="javascript:void(0)">E63</a></li>
		                        <li><a href="javascript:void(0)">E180</a></li>
		                        <li><a href="javascript:void(0)">E200</a></li>
		                    </ul>
		                </li>
		            </ul>
		        </li>
		    </ul>
		    <!---类型-->
		    <ul id="sortUl" :style="{display: sliderDisplay[2]}" @click.stop>
		        <li><a href="javascript:void(0)">欧版现货</a></li>
		        <li><a href="javascript:void(0)">港版现货</a></li>
		    </ul>
		    <!--确认筛选等-->
		    <div class="fix_btn" :class="{fixIn:showHideBool,fixOut:!showHideBool}" @click.stop>
		        <div class="fix_content">
		            <strong>筛选条件：</strong>
		            <span v-if="condition!=''">
		            	{{condition}}<input type="hidden" :value="condition" name="brandUl">
		            </span>
		        </div>
		        <div class="fix_button">
		            <input class="fix_submit" type="button" value="确认" />
		            <input class="fix_cancel" type="button" value="取消" />
		        </div>
		    </div>
		</div>
	</div>
</template>

<script lang="babel">
	
	export default{
		data(){
			return {
				condition:'',
				filterNameArr:["品牌","外色","内色","车型"],
				showHideBool:false,//显示fix层的boolean值
				sliderDisplay:["block","none","none"],//显示fix层中的菜单
			}
		},
		methods:{
			searchSlider(num){//切换
				this.showHideBool=true;//this指组件对象

				for(let i=0;i<this.sliderDisplay.length;i++){
					//console.log(num,i);
					if(i!=num){
						this.sliderDisplay[i]="none";
					}
				}
				this.sliderDisplay[num]="block";
			},
			filterSearch(str){
				this.condition=str;
			}
		},
        components:{
  
        }
    }
</script>

<style>
.list{ margin-top: 0;}
.list .mg_list{width: 100%;}
.list .mg_ifo{ width: 4.6rem;}
.filter{ width: 100%; height: 0.82rem; line-height: 0.82rem; background: #fff;}
.filter li{ float: left; width: 33.33%; text-align:center; color: #666666; font-size: 0.26rem;}
.fix{ position:fixed; left: -100%; top: 0; background: rgba(0,0,0,0.6); z-index: 9999; width: 100%; height: 100%; overflow-y: auto;}
.fix ul{ width: 75%; background: #fff; height: 100%;}
.fix ul li{ box-sizing: border-box; padding-left: 0.5rem; line-height: 0.8rem; border-bottom:#999999 1px solid; font-size: 0.32rem; background: #fff;}
.fix ul a{ display: block; width: 100%; height: 100%; outline: none;}
.fix ul ul{ width: 100%; height: auto; padding-bottom: 0.2rem;}
.fix ul ul ul li{ border-bottom: #999 1px dashed;}
.fix_btn{ position:fixed;z-index: 9; left: -100%; bottom: 0; width: 75%; background: #fff;}
.fix_button{ height: 0.8rem; width: 100%; padding-top: 0.2rem;}
.fix_cancel,.fix_submit{ float: right; width: 1.1rem; height: 0.6rem; text-align:center; line-height: 0.6rem; border: 0 none; margin-right: 0.15rem; color: #fff; font-size: 0.3rem;}
.fix_submit{ background: #d31131;}
.fix_cancel{ background: #999;}
.fix_content{ line-height:0.5rem; font-size: 0.3rem; padding: 0.3rem; overflow: hidden;}
.fix_content strong,.fix_content span{ float: left; margin-right: 0.15rem;}
.fix_content span{border: #000 1px solid; padding: 0 0.05rem;}
.fix_pp{ padding-bottom: 8rem; box-sizing: border-box;}
@keyframes fixIn
{
    0%{ left: -100%;}
    100%{ left: 0;}
}

@-moz-keyframes fixIn /* Firefox */
{
    0%{ left: -100%;}
    100%{ left: 0;}
}

@-webkit-keyframes fixIn /* Safari 和 Chrome */
{
    0%{ left: -100%;}
    100%{ left: 0;}
}

@-o-keyframes fixIn /* Opera */
{
    0%{ left: -100%;}
    100%{ left: 0;}
}
.fixCur{
    animation: fixIn 0.5s forwards;
    -moz-animation: fixIn 0.5s forwards;	/* Firefox */
    -webkit-animation: fixIn 0.5s forwards;	/* Safari 和 Chrome */
    -o-animation: fixIn 0.5s forwards;	/* Opera */
}

@keyframes fixOut
{
    0%{ left: 0;}
    100%{ left: -100%;}
}

@-moz-keyframes fixOut /* Firefox */
{
    0%{ left: 0;}
    100%{ left: -100%;}
}

@-webkit-keyframes fixOut /* Safari 和 Chrome */
{
    0%{ left: 0;}
    100%{ left: -100%;}
}

@-o-keyframes fixOut /* Opera */
{
    0%{ left: 0;}
    100%{ left: -100%;}
}
.fixIn{
    animation: fixIn 0.5s forwards;
    -moz-animation: fixIn 0.5s forwards;	/* Firefox */
    -webkit-animation: fixIn 0.5s forwards;	/* Safari 和 Chrome */
    -o-animation: fixIn 0.5s forwards;	/* Opera */
}
.fixOut{
    animation: fixOut 0.5s forwards;
    -moz-animation: fixOut 0.5s forwards;	/* Firefox */
    -webkit-animation: fixOut 0.5s forwards;	/* Safari 和 Chrome */
    -o-animation: fixOut 0.5s forwards;	/* Opera */
}
</style>


