<template>
	<div>
		<ul class="manage_list list">
		    <li v-for="obj in listArr">
		        <a class="mg_list" :href="obj.href">
		            <span class="mg_img"><img :src="obj.imgUrl" /></span>
		            <dl class="mg_ifo">
		                <dt>{{obj.title}}</dt>
		                <dd>
		                    <span class="fl" v-if="obj.address">{{obj.address}}</span>
		                    <span class="fl" v-if="obj.money">{{obj.money}}<i>万元</i></span>
		                    <span class="fr" v-if="obj.payType">{{obj.payType}}</span>
		                    <span class="fr" v-if="obj.lookNum">浏览 {{obj.lookNum}}</span>
		                </dd>
		            </dl>
		        </a>
		    </li>
	    </ul>
	</div>
</template>

<script lang="babel">
	export default{
		props: {//从父得到数据
			listArr:Array
		},
		data(){
			return {
				
			}
		},
        components:{
  
        }
    }
</script>

<style>
#publish{ display: block; background: #fff; color: #dadc3c; font-size: 0.26rem; line-height: 0.65rem; width: 3.8rem; height: 0.65rem; border: #ecee11 1px solid; border-radius: 2px; margin: 0.24rem auto 0; text-align: center;}
.manage_list{ margin-top: 0.55rem; width: 100%; overflow: hidden; background: #fff;}
.manage_list li{ overflow: hidden; width: 110%; margin-top: 0.24rem;}
.mg_check{ float: left; width: 0.48rem; height: 0.48rem; margin: 0.43rem 0.08rem 0 0.23rem; background: url(../../assets/images/icon_bg.png) no-repeat -1.85rem -1.51rem;  background-size: 2.43rem 2.1rem; overflow:hidden;}
.mg_check input{ opacity: 0; margin-left: -100px;}
.mg_checkCur{ background-position:-1.85rem -0.89rem;}
.mg_list{ float: left; width:6.75rem; border-bottom: #e5e5e5 1px solid; padding-bottom: 0.21rem;}
.mg_img{ float: left; width: 1.98rem; height: 1.48rem; border: #e5e5e5 1px solid; margin-left: 0.16rem;}
.mg_img img{width: 100%; height: 100%;}
.mg_ifo{ float: left; width: 3.9rem; margin-left: 0.27rem;}
.mg_ifo dt{ line-height: 0.4rem; height: 0.8rem; overflow: hidden; font-size: 0.32rem; font-weight: bold;}
.mg_ifo  dd{ height: 0.62rem; margin-top: 0.11rem; width: 100%;}
.mg_ifo  dd .fl{ line-height: 0.62rem; color: #d5d713; font-size: 0.32rem;}
.mg_ifo  dd .fl i{ font-style: normal; font-size: 0.2rem;}
.mg_ifo  dd .fr{ color: #c1c1c1; font-size: 0.2rem; margin-top: 0.22rem; line-height: 0.4rem;}
.mg_btn{ display: block; width: 100%;border: 0 none; border-top: #c7c7cd 1px solid; height: 0.99rem; line-height: 0.99rem; text-align: center; background: #d31131; color: #fff; font-weight: bold; font-size: 0.34rem;}
.list{ margin-top: 0;}
.list .mg_list{width: 100%;}
.list .mg_ifo{ width: 4.6rem;}
.filter{ width: 100%; height: 0.82rem; line-height: 0.82rem; background: #fff;}
.filter li{ float: left; width: 33.33%; text-align:center; color: #666666; font-size: 0.26rem;}
</style>


