<template>
	<div>
		<search></search>
		<list :listArr="listSellArr"></list>
		<footerA></footerA>
	</div>
</template>

<script lang="babel">
	import search from "./common/search";
	import list from "./common/sellByList";
	import footerA from "./common/footer";
	export default{
		data(){
			return{
				listSellArr:[]
			}
		},
		components:{
			list,
			footerA,
			search
		},
		mounted(){//等节点装载完毕时触发
			//console.log(this.$http);
			let _this=this;//组件对象
			_this.$http({
				method:"get",//请求方式
				url:"/static/json/sell.json",
				params:{},//发送的数据；get请求用属性params，post请求用属性body	
			}).then(function(data){//success;data后台返回的数据
				//console.log(data,data.data,data.body);
				//console.log(_this.listSellArr);
				_this.listSellArr=data.body;
			},function(err){//error
				console.log(err);
			});
		}
	}
</script>
<style>

</style>


