<template>
	<main class="app">
		<router-view></router-view>
    </main>
</template>

<script lang="babel">
    import reponeFont from './assets/js/reponeFont.js';//
    reponeFont.init();//字体响应
    
	export default{
        
    }
</script>

<style>
    [v-cloak]{ display: none;}
    body,h1,h2,h3,h4,div,ul,li,dl,dt,dd,form,input,select,option,textarea,a,img,em,i,strong,b{ margin: 0; padding: 0;}
    li{ list-style: none;}s
    input,select,option,textarea{font-family:"微软雅黑";outline: none; resize: none;}
    a{ text-decoration: none; color: #000;}
    a:hover{ color: #000;}
    a:visited{ color: #000;}
    img{ vertical-align: top; border: 0 none;}
    html{ font-size: 625%; box-sizing: border-box; padding-bottom:1em;}
    body{ max-width: 750px; min-width: 320px; margin: 0 auto; font-family:"微软雅黑"; background: #f7f7f7;}
    .clearfix:after{ content: "."; display: block; visibility: hidden; clear: both; height: 0; font-size: 0;}
    .body_bg1{ background: #f1f1f1;}
    input[type=button],
    input[type=submit],
    input[type=file],
    button {
      cursor: pointer;
      -webkit-appearance: none;
      /*去除ip按钮渐变*/
    }
    /*公共样式*/
.fl{ float: left !important;}
.fr{ float: right !important;}
</style>


